<script>

import { getHourTime, getZeroPad } from '../Filters'

export default {

	data(){

	return{
       
         displayContentColor:{
            
            backgroundColor:"green",
            tipoAnimacao: 'fade'

   


       		},

       	 tvContent:{

       	 		image: true,
            relogio: "",
            hours: "00",
            minutes: "00",

       	 	}
       	 
       	}
    },

   mounted () {
    this.$options.interval = setInterval(this.updateDateTime, 1000);
  },
   beforeDestroy () {
    clearInterval(this.$options.interval);
  },


	methods: {

		powerAndOffDisplayTv(){


			if(this.tvContent.image == true){

			this.tvContent.image = false

			this.displayContentColor.backgroundColor = "red"


			}else{

			this.tvContent.image = true

			this.displayContentColor.backgroundColor = "green"
			

			}

    },

    updateDateTime () {
      let now = new Date()
      this.tvContent.hours = now.getHours()
      this.tvContent.minutes = getZeroPad(now.getMinutes())
        
       },


    afterEnter(elemento){

    console.log('afterEnter')

    },

    enterCancelled(){

      console.log('enterCancelled')

    },

    //ANIMAÇOES DE SAIDA
    leave(elemento, done){

    
      this.animar(elemento, done, true)


    },


    afterLeave(elemento){

      console.log('afterEnter')

    },

    leaveCancelled(){

      console.log('leaveCancelled')

    } 
    }
}



</script>

